<template>
  <div id="root">
    <div class="todo-container">
      <div class="todo-wrap">
        <MyHeader @addTodo="addTodo"></MyHeader>
        <MyList :todos="todos"></MyList>
        <MyFooter :todos="todos" @clearDoneTodo="clearDoneTodo"></MyFooter>
      </div>
    </div>
  </div>
</template>

<script>
import PubSub from "pubsub-js";
import MyHeader from "./components/MyHeader";
import MyList from "./components/MyList";
import MyFooter from "./components/MyFooter";

export default {
  name: "App",
  components: {
    MyFooter,
    MyList,
    MyHeader
  },
  data() {
    return {
      todos: [
        {id: '001', title: '吃饭', done: true},
        {id: '002', title: '睡觉', done: false},
        {id: '003', title: '敲代码', done: true}
      ]
    }
  },
  methods: {
    addTodo(todoObj) {
      this.todos.unshift(todoObj);
    },
    checkTodo(todo) {
      todo.done = !todo.done;
    },
    deleteTodo(id) {
      this.todos = this.todos.filter((todo) => {
        return todo.id !== id
      })
    },
    updateTodo(todo, title) {
      todo.title = title;
    },
    checkAllTodo(_, done) {
      this.todos.forEach((todo) => {
        todo.done = done;
      })
    },
    clearDoneTodo() {
      this.todos = this.todos.filter((todo) => {
        return !todo.done
      })
    }
  },
  mounted() {
    this.$bus.$on('checkTodo', this.checkTodo);
    this.$bus.$on('deleteTodo', this.deleteTodo);
    this.$bus.$on('updateTodo', this.updateTodo);
    this.pubId = PubSub.subscribe('checkAllTodo', this.checkAllTodo);
  },
  beforeDestroy() {
    this.$bus.$off(['checkTodo', 'deleteTodo', 'updateTodo']);
    PubSub.unsubscribe(this.pubId);
  }
}
</script>

<style>
  /*base*/
  body {
    background: #fff;
  }

  .btn {
    display: inline-block;
    padding: 4px 12px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }

  .btn-danger {
    color: #fff;
    background-color: #da4f49;
    border: 1px solid #bd362f;
  }

  .btn-danger:hover {
    color: #fff;
    background-color: #bd362f;
  }

  .btn-edit {
    color: #fff;
    background-color: #49bdda;
    border: 1px solid #26bbbb;
    margin-right: 5px;
  }

  .btn-edit:hover {
    color: #fff;
    background-color: #26bbbb;
  }

  .btn:focus {
    outline: none;
  }

  .todo-container {
    width: 600px;
    margin: 0 auto;
  }
  .todo-container .todo-wrap {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
</style>